import React, { useState } from 'react'
import ReactDOM from 'react-dom'
import data from './data'
class MyForm extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      username: '',
      age: null,
      filename: null,
    }
  }
  myFileChangeHandler = (event) => {
    event.nativeEvent.stopImmediatePropagation()
    let nam = event.target.name
    let val = event.target.value

    this.setState({
      [nam]: val,
      file: URL.createObjectURL(event.target.files[0]),
    })
  }
  myTextChangeHandler = (event) => {
    event.nativeEvent.stopImmediatePropagation()
    let nam = event.target.name
    let val = event.target.value

    if (nam === 'age') {
      if (!Number(val)) {
        alert('Your age must be a number')
      }
    }

    this.setState({
      [nam]: val,
    })
  }
  handleSubmit = (event) => {
    event.preventDefault()
  }
  render() {
    return (
      <div className="form">
        <form>
          <h2 className="title">
            Today is the Birtday of {this.state.username} of {this.state.age}{' '}
            Years Old
          </h2>
          <div className="center">
            <img src={this.state.file} alt="Birtday Pic" />
          </div>

          <p>Enter the name of the Birtday Person:</p>
          <input
            type="text"
            name="username"
            onChange={(this.myFileChangeHandler, this.myTextChangeHandler)}
          />
          <p>Enter his/her age:</p>
          <input
            type="text"
            name="age"
            onChange={(this.myFileChangeHandler, this.myTextChangeHandler)}
          />
          <p>Click on the chosen button to send the birtday pic</p>
          <input
            type="file"
            id="myFile"
            name="filename"
            onChange={this.myFileChangeHandler}
          ></input>
          <button
            type="button"
            onClick={() => document.getElementById('myFile').click()}
            className="send"
          >
            Send the Birtday Picture
          </button>
          <input
            type="submit"
            value="Submit"
            onClick={this.handleSubmit}
          ></input>
        </form>
      </div>
    )
  }
}

export default MyForm
